/**
 * Base
 */
html,
body {
  width: 100%;
  height: 100%;
}

html {
  font-size: 16px;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

body {
  font-family: "Open Sans", "Trebuchet MS", sans-serif;
  font-size: 100%;
  font-weight: 400;
  color: #212121;
  line-height: 1.45;
}

/* Avoid empty images to appear as broken */
img:not([src]):not([srcset]) {
  visibility: hidden;
}

/**
 * Typography
 */
a {
  text-decoration: none;
  transition: all 0.25s cubic-bezier(0.4, 0, 1, 1);
}

a:focus, a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Raleway', 'Open Sans', 'Trebuchet MS', sans-serif;
}

ul {
  padding: 0;
  list-style-type: none;
}

/**
 * Animations atom
 */
@keyframes gradient-animation {
  0% {
    background-position: 50% 0;
  }
  50% {
    background-position: 51% 100%;
  }
  100% {
    background-position: 50% 0;
  }
}

/**
 * Buttons atom
 */
.btn__container {
  text-align: center;
}

.btn {
  font-size: 0.875rem;
  text-transform: uppercase;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
  transition: all 0.25s cubic-bezier(0.4, 0, 1, 1);
}

.btn:active, .btn:focus, .btn:hover {
  color: #fff;
  box-shadow: 0 26px 35px rgba(50, 50, 93, 0.1), 0 14px 15px rgba(0, 0, 0, 0.07);
}

.btn--fab {
  display: block;
  font-size: 1.5rem;
  color: #fff;
  text-align: center;
  border-radius: 50%;
}

.btn--fab--primary {
  background-color: #ffc107;
}

.btn--fab--primary:focus, .btn--fab--primary:hover {
  background-color: #ffca2b;
}

.btn--fab--primary:active {
  background-color: #e2aa00;
}

.btn--fab--big {
  padding: 1rem;
  width: 3.5rem;
  height: 3.5rem;
}

.btn--fab--medium {
  padding: .35rem;
  width: 3rem;
  height: 3rem;
  font-size: 1.5rem;
}

.btn--fab--small {
  padding: .35rem;
  width: 2.5rem;
  height: 2.5rem;
}

.btn--full,
.btn--ghost {
  padding: 0.5625rem 2.625rem;
  border: 2px solid;
  font-weight: 700;
}

.btn--full--black {
  color: #fff;
  background-color: #181818;
  border-color: #181818;
}

.btn--full--black:focus, .btn--full--black:hover {
  background-color: #3e3e3e;
  border-color: #3e3e3e;
}

.btn--full--primary {
  color: #fff;
  background-color: #ffc107;
  border-color: #ffc107;
}

.btn--full--primary:focus, .btn--full--primary:hover {
  background-color: #ffd454;
  border-color: #ffd454;
}

.btn--full--white {
  color: #212121;
  background-color: #fff;
  border-color: #fff;
}

.btn--full--white:focus, .btn--full--white:hover {
  color: #474747;
}

.btn--ghost {
  background: transparent;
}

.btn--ghost--black {
  color: #181818;
  border-color: #181818;
}

.btn--ghost--black:focus, .btn--ghost--black:hover {
  color: #3e3e3e;
  border-color: #3e3e3e;
}

.btn--ghost--primary {
  color: #ffc107;
  border-color: #ffc107;
}

.btn--ghost--primary:focus, .btn--ghost--primary:hover {
  color: #ffd454;
  border-color: #ffd454;
}

.btn--ghost--white {
  color: #fff;
}

.btn--ghost--white:focus, .btn--ghost--white:hover {
  color: #f5f5f5;
}

.btn--round, .el--round {
  border-radius: 50px;
}

/**
 * Dividers atom
 */
.divider {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  max-width: 4rem;
  height: 2px;
  background-color: #ffc107;
  border: 0;
}

.divider--left {
  margin-left: 0;
}

/**
 * Icons atom
 */
.icon {
  color: #a7cef7;
  cursor: pointer;
  transition: color 0.25s cubic-bezier(0.4, 0, 1, 1);
}

.icon:focus, .icon:hover {
  color: #fff;
}

.icon--menu {
  float: right;
}

.process__icon {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.process__icon img {
  max-width: 5.625rem;
  max-height: 5.625rem;
}

@media screen and (max-width: 767px) {
  .process__info {
    margin-right: auto;
    margin-left: auto;
  }
}

/**
 * Links atom
 */
.link--default {
  color: #86878b;
}

.link--default:focus, .link--default:hover {
  color: #212121;
}

.link--black {
  color: #212121;
}

.link--black:focus, .link--black:hover {
  color: #f5f5f5;
}

.link--gold {
  color: #f5f5f5;
}

.link--gold:focus, .link--gold:hover {
  color: #212121;
}

.link--white {
  color: #fff;
}

.link--white:focus, .link--white:hover {
  color: #757575;
}

/**
 * Typed
 */
.no-js .js-typed {
  display: none;
}

.no-js .js-typed-fallback {
  display: block;
}

.js .js-typed-fallback {
  display: none;
}

/**
 * Cards molecule
 */
.card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 1rem 2rem;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
}

@media screen and (max-width: 702px) {
  .card:nth-of-type(2) {
    margin-top: 2.5rem;
  }
}

@media screen and (max-width: 1070px) {
  .card:last-of-type {
    margin-top: 2.5rem;
  }
}

.card--hero {
  margin-right: auto;
  margin-left: auto;
}

@media screen and (min-width: 769px) {
  .card--hero {
    float: right;
  }
}

.card--long {
  padding: 2rem 2rem 3rem;
  width: 16.5rem;
  min-height: 24rem;
}

.card--small {
  max-width: 22.5rem;
}

.card--square {
  width: 21.5rem;
  min-height: 16.5rem;
}

/**
 * Countdown molecule
 */
.countdown {
  display: table;
  margin: 0 auto 1.5rem;
  font-size: 1.75rem;
  font-weight: 500;
}

.countdown > div {
  float: left;
  min-width: 5rem;
  color: #fff;
}

@media screen and (max-width: 369px) {
  .countdown > div:nth-of-type(3) {
    clear: left;
  }
}

.countdown span {
  position: relative;
  display: block;
  padding-top: 0.125rem;
  margin-top: 0.375rem;
  font-size: 1rem;
  color: #fff;
  text-align: center;
}

.countdown span:before {
  position: absolute;
  top: -0.25rem;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  width: 1.25rem;
  height: 0.0625rem;
  content: '';
  background: #fff;
}

/**
 * Features molecule
 */
.features__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

@media screen and (max-width: 767px) {
  .features__list > div {
    margin-bottom: 1rem;
  }
}

@media screen and (max-width: 767px) {
  .features__list p {
    margin-right: auto;
    margin-left: auto;
  }
}

.features__mockup {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.features__mockup img {
  max-width: 100%;
}

.features__icon {
  padding: 0.75rem 0.8125rem;
  color: #fff;
  background-color: #ffc107;
  border-radius: 50%;
}

/**
 * Forms molecule
 */
.form-group {
  position: relative;
  padding-left: 0;
  margin-bottom: 2rem;
}

@media screen and (max-width: 550px) {
  .form-group:first-of-type {
    margin-bottom: 2rem;
  }
}

label,
.form-label {
  position: absolute;
  top: 0.5rem;
  left: 0;
  z-index: 1;
  margin-bottom: 0;
  display: block;
  font-size: 0.875rem;
  font-weight: 400;
  color: #757575;
  transition: all 0.25s cubic-bezier(0.4, 0, 1, 1);
}

input,
select,
textarea,
.form-element {
  position: relative;
  z-index: 3;
  display: block;
  padding-bottom: 0.5rem;
  margin-bottom: 0.5rem;
  width: 100%;
  font-family: "Open Sans", "Trebuchet MS", sans-serif;
  background-color: transparent;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid #e0e0e0;
  border-left: 0;
}

input:focus,
select:focus,
textarea:focus,
.form-element:focus {
  outline: 0;
  border-bottom-color: #ffc107;
}

.form-element:focus + label,
.form-element:focus + .form-label {
  font-size: 0.75rem;
  font-weight: 700;
  color: #ffc107;
  transform: translateY(-26px);
}

.form textarea {
  resize: vertical;
}

.form--contact textarea {
  min-height: 6.25rem;
}

@media screen and (min-width: 768px) {
  .form--contact textarea {
    min-height: 12.1875rem;
  }
}

.form--hero {
  margin-top: 16px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 15.9375rem;
}

.form--optin {
  margin-right: auto;
  margin-left: auto;
  max-width: 30rem;
}

.form--optin .form-group {
  position: relative;
}

.form--optin input {
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
  padding-right: 1.75rem;
  padding-left: 1.75rem;
  background-color: #fff;
  border: 1px solid #fff;
  box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
}

.form--optin input:focus {
  border: 1px solid #ffc107;
}

@media screen and (min-width: 440px) {
  .form--optin input {
    padding-right: 11.75rem;
  }
}

@media screen and (max-width: 439px) {
  .form--optin .btn {
    width: 100%;
    box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
  }
}

@media screen and (min-width: 440px) {
  .form--optin .btn {
    position: absolute;
    top: 0.1875rem;
    right: 0.25rem;
    z-index: 3;
    box-shadow: none;
  }
}

/**
 * Heroes molecule
 */
.hero {
  position: relative;
  background-position: center center;
  background-size: cover;
}

@media screen and (min-width: 768px) {
  .hero {
    height: 100vh;
    min-height: 40rem;
  }
}

.hero--gradient {
  background: #56ccf2;
  /* fallback for old browsers */
  background: linear-gradient(to top, #56ccf2, #2f80ed);
  /* uncomment the lines below to turn on animated gradient */
  /* background-size: 400% 400%;
  animation: gradient-animation 15s ease infinite;*/
}

.hero--gradient .hero__overlay {
  background-color: rgba(0, 0, 0, 0.01);
}

.hero--image {
  background-image: url(../../images/bcgs/hero.jpg);
}

@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .hero--image {
    background-image: url(../../images/bcgs/hero--@2.jpg);
  }
}

.hero--optin {
  min-height: 50rem;
  height: 100vh;
}

@media screen and (min-width: 540px) {
  .hero--optin {
    min-height: 33.75rem;
  }
}

.hero--home {
  min-height: 71rem;
}

@media screen and (min-width: 555px) {
  .hero--home {
    min-height: 60rem;
  }
}

.hero__overlay {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.45);
}

.hero__content {
  width: 100%;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.hero__content .list {
  align-items: center;
}

.hero__content h1 {
  font-size: 3rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.95);
  text-transform: uppercase;
}

@media screen and (max-width: 767px) {
  .hero__content h1 {
    text-align: center;
  }
}

.hero__content p {
  margin-bottom: 28px;
  color: rgba(255, 255, 255, 0.75);
}

@media screen and (max-width: 767px) {
  .hero__content p {
    margin-right: auto;
    margin-left: auto;
    text-align: center;
  }
}

@media screen and (min-width: 768px) {
  .hero__content .card {
    margin-top: 0;
  }
}

/* Hero - Carousel */
.hero--carousel {
  overflow: hidden;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.hero--carousel .carousel__wrapper,
.hero--carousel .carousel-inner,
.hero--carousel .item,
.hero--carousel .carousel__item {
  min-height: 71rem;
  height: 100%;
}

@media screen and (min-width: 555px) {
  .hero--carousel .carousel__wrapper,
  .hero--carousel .carousel-inner,
  .hero--carousel .item,
  .hero--carousel .carousel__item {
    min-height: 60rem;
  }
}

.hero--carousel .item {
  transition: opacity .95s;
}

.hero--carousel .item.active {
  animation: fade-in .95s;
}

.hero--carousel .carousel__item {
  width: 100%;
  height: 100%;
  background-size: cover;
}

.hero--carousel .carousel__item-one {
  background: url(../../images/carousel/carousel-slide-one.jpg) no-repeat center center;
}

.hero--carousel .carousel__item-two {
  background: url(../../images/carousel/carousel-slide-two.jpg) no-repeat center center;
}

.hero--carousel .carousel__item-three {
  background: url(../../images/carousel/carousel-slide-three.jpg) no-repeat center center;
}

.hero--carousel .carousel__item-four {
  background: url(../../images/carousel/carousel-slide-four.jpg) no-repeat center center;
}

/* Hero -  ParticlesJS */
.hero--particlesjs {
  background-color: #fff;
  background-image: none;
}

.hero--particlesjs .hero__overlay {
  background-color: rgba(255, 255, 255, 0.25);
}

.hero--particlesjs .hero__content h1,
.hero--particlesjs .hero__content p {
  color: #212121;
}

.particles {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  opacity: .3;
}

/* Hero - Video */
.hero--video {
  background-image: none;
  overflow: hidden;
}

.video-container {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
}

.video-container video {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  display: block;
  min-width: 100% !important;
  min-height: 100% !important;
  object-fit: fill;
}

.hero--countdown video {
  max-width: 100%;
  max-height: 100%;
}

/**
 * Inserts molecule
 */
.insert {
  position: relative;
}

.insert--logos {
  padding-top: 1.75rem;
  padding-bottom: 1.75rem;
}

.insert--logos ul {
  margin-bottom: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  overflow: hidden;
}

